/**
 *  obj  url 
 * @param {*} obj -  obj  JSON 
 * 
 * @example
 * 
 * wt.url.paramsify({a: 3, b: 4, c: {d: 5}})
 * // => a=3&b=4&c={"d":5}
 */
exports.paramsify = obj => {
    if (typeof obj !== 'object') return ''

    return Object.keys(obj).map(key => {
        if (typeof obj[key] === 'object') return `${key}=${JSON.stringify(obj[key])}`
        return `${key}=${obj[key]}`
    }).join('&')
}


