const {sleep} = require('../time/index')

/**
 *  func  times 
 * @param {function} func - 
 * @param {number} times - 
 * 
 * @example
 * 
 * //  3 
 * await wt.run.runTimes(fetch('www.baidu.com'), 3)
 */
const retryRun = async (func, times = 5) => {
    let count = times || 5
    while (count--) {
        try {
            return await func()
        } catch (err) {
            if (!count) throw err
        }
    }
}

/**
 *  func  timeout 
 * @param {function} func - 
 * @param {number} timeout - ms
 * 
 * @example
 * //  5 
 * await wt.run.waitRun(fetch('www.baidu.com'), 5000)
 */
const waitRun = async (func, timeout) => {
    return Promise.race([
        func(),
        new Promise((r, j) => setTimeout(j(new Error('')), timeout))
    ])
}

/**
 *  interval 
 * @param {function} func - 
 * @param {number} times - 
 * @param {number} interval - 
 */
const runTimes = async (func, times = 5, interval) => {
    for (let i = 0; i < times; i++) {
        await func()
        if (interval > 0) await sleep(interval)
    }
}

module.exports = {
    retryRun,
    waitRun,
    runTimes
}


